.statistics {
  /* padding: 0 auto; */
  margin: 0 70px;
  padding: 0 70px;
}
.title {
  display: flex;
  justify-content: center;
  margin: 0;
  text-transform: uppercase;
  color: lightslategray;
  background-color: #ebd5d5;
}
.statList {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: center;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
  color: lightslategray;
  background-color: rgb(240, 251, 255);
}
.item {
  display: block;
  /* margin: 10px; */
  padding: 15px;
  justify-content: center;
  text-align: center;
  /* background-color: grey; */
}
.label {
  display: flex;
}
